import React, {Component} from 'react';
import {emit, subscribe} from "event-subscription";
import {SUBSCRIBE_ACTIVE_SECTION, SUBSCRIBE_NAV_DRAWER} from "../../constants";
import "./Sidenav.scss"

class Sidenav extends Component {

    state = {
        merchantPopup: false,
        navDrawerOpen: false,
        activeSection: 'HeaderComponent',
        options: [
            {
                title: "Home",
                scrollTo: 0,
                activeId: "HeaderComponent"
            }, {
                title: "About",
                scrollTo: "About"
            }, {
                title: "Our Team",
                scrollTo: "TeamComponent"
            }, {
                title: "How It Works",
                scrollTo: "StepsComponent"
            }, {
                title: "Contact Us",
                scrollTo: "ContactUs"
            },
        ]
    };

    sideDrawerSubscription = null;
    activeSectionSubscription = null;

    componentWillMount() {

        this.sideDrawerSubscription = subscribe(SUBSCRIBE_NAV_DRAWER, this.toggleNavDrawer);
        this.activeSectionSubscription = subscribe(SUBSCRIBE_ACTIVE_SECTION, this.setActiveSection)

    }

    componentWillUnmount() {

        this.sideDrawerSubscription.unsubscribe();
        this.activeSectionSubscription.unsubscribe();

    }

    changePage = (path) => {

    };

    setActiveSection = (id) => {
        this.setState({
            activeSection: id
        })
    };

    toggleNavDrawer = (override) => {
        this.setState(state => {
            return {
                ...state,
                navDrawerOpen: typeof override === 'boolean' ? override : !state.navDrawerOpen
            }
        })
    };

    render() {

        const {navDrawerOpen, options,activeSection} = this.state;
        const {scrollTo} = this.props;

        return (
            <div id="SidenavCmpnt">
                <div
                    className={navDrawerOpen ? "display-block" : "display-none"}
                    id="sidenavTint" onClick={this.toggleNavDrawer}/>

                <div id="sidenavContent"
                     className={navDrawerOpen ? "navOpen" : "navClosed"}>

                    <div>
                        <h3 className="text-align-center">Linguartech</h3>
                    </div>

                    {options.map((option, key) => {
                        return <button key={key} className={"sidenavButton " + (activeSection === (option.activeId || option.scrollTo) ? "selectedSidenavButton" : "")} onClick={() => {
                            scrollTo(option.scrollTo)
                        }}> {option.title}</button>
                    })}


                    <span className="flex-1"/>


                </div>
            </div>
        )
    }
}


export default Sidenav;

